import React, {useState} from 'react'
import { Navbar, Nav } from 'react-bootstrap';
import { Link, withRouter } from 'react-router-dom';


const NavBar = (props: any) => {
    const pathname = props.location.pathname;
    // console.log(pathname)
    const [expanded, setExpanded] = useState(false);

    return (
        <>
            <style type="text/css">
                {`
                .bg-dark {
                    background-color: #0d090b!important;
                }
                `}
            </style>
            <Navbar fixed="top" expanded={expanded} collapseOnSelect expand="lg" variant="dark" bg="dark">
                <Navbar.Brand as={Link} to="/home">Koops</Navbar.Brand>
                <Navbar.Toggle onClick={() => setExpanded(!expanded)} aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="mr-auto">
                        <Nav.Link onClick={() => setExpanded(!expanded)} as={Link} to="/home" active={pathname.startsWith('/home')}>Home</Nav.Link>
                        <Nav.Link onClick={() => setExpanded(!expanded)} as={Link} to="/services" active={pathname.startsWith('/services')}>Service</Nav.Link>
                        <Nav.Link onClick={() => setExpanded(!expanded)} as={Link} to="/contact" active={pathname.startsWith('/contact')}>Contact Us</Nav.Link>
                    </Nav>
                    <Nav>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        </>
    )
}

export default withRouter(NavBar)
